import {GlobalComponent} from '../../components/global/global';
import { LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
} from "ionic-angular";
import { ConnHttpViewModel } from "../../Model/ViewModel/ConnHttpViewModel";

/**
 * Generated class for the ConnModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-conn-http-add-edit-modal",
  templateUrl: "conn-http-add-edit-modal.html"
})
export class ConnHttpAddEditModalPage {
  item: any;
  mode: string;
  CanEditConn: boolean;
  title: string;
  Conn_type: string;
  confirm_pwd:string;
  change_mode:string;
  sql_help:string=`
可輸入下面方式
Authorization 密碼欄位輸入Token 登入帳號:xxxxxxxxxx(例如:帳號=Authorization,密碼=Token admin:eyJhbGciOiJI)
Authorization 密碼欄位輸入Bearer xxxxxxxxxx(例如:帳號=Authorization,密碼=eyJhbGciOiJI)
NTLM:本機帳號 密碼欄位輸入純密碼(例如:帳號=NTLM:admin,密碼=12345678)
NTLM:網域\帳號 密碼欄位輸入純密碼(例如:帳號=NTLM:SYSTEX\admin,密碼=12345678)
純帳號 密碼欄位輸入純密碼(例如:帳號=admin,密碼=12345678)
  `
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    private global: GlobalComponent,
  ) {
    this.mode = navParams.data.mode;
    this.change_mode=navParams.data.change_mode;
    this.item = new ConnHttpViewModel();
    this.item.remote_conn_id = navParams.data.item.remote_conn_id;
    this.item.user_name = navParams.data.item.user_name;
    this.item.conn_string = navParams.data.item.conn_string;
    this.item.pwd = navParams.data.item.pwd;
    this.confirm_pwd=navParams.data.item.pwd;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.item.conn_type_key = navParams.data.item.conn_type_key;
    this.item.conn_type = navParams.data.item.conn_type;
    if(this.change_mode==="c")
      this.item.remote_conn_id=this.item.remote_conn_id+"_copy";
    this.CanEditConn = navParams.data.CanEditConn;

    this.title = "Http連線";
  }
  Help() {

    this.global.createLoader();

    this.global.loading.present().then(() => {
       this.global.showMessage("帳號說明",this.sql_help);

    });

  }
  Save() {
    if(this.item.pwd>"" && this.confirm_pwd!=this.item.pwd)
    {
      this.global.showPopup("密碼錯誤","確認密碼不一致");
      return;
    }
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad ConnModalPage");
  }
}



